import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-blank',
  templateUrl: './blank.component.html',
  styleUrls: ['./blank.component.css']
})
export class BlankComponent implements OnInit {

  constructor() { }
  goToDiv(e,id){
    e.preventDefault();
        $('html,body').animate({
            scrollTop: $("#"+id).offset().top - 80},
        'slow');
  }
  ngOnInit() {
  }

}



